<template><section>

<h1>Dialog 对话框</h1>
<p>在保留当前页面状态的情况下，告知用户并承载相关操作。</p>
<h3>基本用法</h3>
<p>Dialog 弹出一个对话框，适合需要定制性更大的场景。</p>
<div class="demo-block">
  <div>
    <o-button type="text" @click="dialogVisiable = true">点击打开 Dialog</o-button>
    <o-dialog :visible="dialogVisiable" title="提示" :before-close="handleClose">
      <span>这是一段信息</span>
      <span slot="footer" class="dialog-footer">
        <o-button @click="handleClose">取 消</o-button>
        <o-button type="primary" @click="dialogVisiable = false">确 定</o-button>
      </span>
    </o-dialog>
  </div>
</div>
<demo-block>
                        <div slot="desc"><o-button type="primary" @click="dialogVisiable = true">点击打开 Dialog</o-button>
<o-dialog :visible="dialogVisiable" title="提示" :before-close="handleClose" width="40%">
  <span>这是一段信息</span>
  <span slot="footer" class="dialog-footer">
    <o-button @click="handleClose">取 消</o-button>
    <o-button type="primary" @click="dialogVisiable = false">确 定</o-button>
  </span>
</o-dialog>

</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">o-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogVisiable = true"</span>
  &gt;</span>点击打开 Dialog<span class="hljs-tag">&lt;/<span class="hljs-name">o-button</span>
&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">o-dialog</span>
  <span class="hljs-attr">:visible</span>=<span class="hljs-string">"dialogVisiable"</span>
  <span class="hljs-attr">title</span>=<span class="hljs-string">"提示"</span>
  <span class="hljs-attr">:before-close</span>=<span class="hljs-string">"handleClose"</span>
  <span class="hljs-attr">width</span>=<span class="hljs-string">"40%"</span>
&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>这是一段信息<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"footer"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"dialog-footer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">o-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleClose"</span>&gt;</span>取 消<span class="hljs-tag">&lt;/<span class="hljs-name">o-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">o-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogVisiable = false"</span>&gt;</span>确 定<span class="hljs-tag">&lt;/<span class="hljs-name">o-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">o-dialog</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">dialogVisiable</span>: <span class="hljs-literal">false</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      handleClose() {
        <span class="hljs-keyword">this</span>.dialogVisiable = <span class="hljs-literal">false</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>自定义内容</h3>
<p>Dialog 组件的内容可以是任意的，甚至可以是表格或表单。</p>
<div class="demo-block show-button">
  <div>
    <o-button type="text" @click="dialogVisiable1 = true">点击打开嵌套Table的Dialog</o-button>
    <o-dialog :visible="dialogVisiable1" title="提示" width="50%" :before-close="handleClose">
      <o-table :data="tableData">
        <template slot-scope="scope">  
            <o-table-column label="日期">{{scope.row.date}}
            </o-table-column>
            <o-table-column label="姓名">{{scope.row.name}}
            </o-table-column>
            <o-table-column label="地址">{{scope.row.address}}
            </o-table-column>
          </template>
      </o-table>
    </o-dialog>
  </div>
</div>
<demo-block>
                        <div slot="desc"><o-button type="text" @click="dialogVisiable1 = true">点击打开嵌套Table的Dialog</o-button>
<o-dialog :visible="dialogVisiable1" title="提示" width="50%" :before-close="handleClose">
  <o-table :data="tableData">
    <template slot-scope="scope">
      <o-table-column label="日期">{{scope.row.date}} </o-table-column>
      <o-table-column label="姓名">{{scope.row.name}} </o-table-column>
      <o-table-column label="地址">{{scope.row.address}} </o-table-column>
    </template>
  </o-table>
</o-dialog>

</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">o-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogVisiable1 = true"</span>
  &gt;</span>点击打开嵌套Table的Dialog<span class="hljs-tag">&lt;/<span class="hljs-name">o-button</span>
&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">o-dialog</span>
