</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>visible</td>
<td>是否显示 Dialog，支持 .sync 修饰符</td>
<td>boolean</td>
<td>true,false</td>
<td>false</td>
</tr>
<tr>
<td>title</td>
<td>Dialog 的标题</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>width</td>
<td>Dialog 的宽度</td>
<td>string</td>
<td>-</td>
<td>30%</td>
</tr>
<tr>
<td>top</td>
<td>Dialog CSS 的 margin-top 的值</td>
<td>string</td>
<td>-</td>
<td>13vh</td>
</tr>
</tbody>
</table>
<h3>Slot</h3>
<table class="table"><thead>
<tr>
<th>name</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>-</td>
<td>Dialog 的内容</td>
</tr>
<tr>
<td>footer</td>
<td>Dialog 按钮操作区的内容</td>
</tr>
</tbody>
</table>
</section></template>
<style lang="less" type="text/less" scoped>
</style>
<script>
  export default {
    data() {
      return {
        dialogVisiable: false,
        dialogVisiable1: false,
        tableData: [{
            date: '2016-05-02',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
          }, {
            date: '2016-05-04',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1517 弄'
          }, {
            date: '2016-05-01',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1519 弄'
          }, {
            date: '2016-05-03',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1516 弄'
          }]
      };
    },
    methods: {
      handleClose(){
        this.dialogVisiable = false
        this.dialogVisiable1 = false
      }
    }
  }
</script>


